import { pxStatics } from "../pxStatics";
import { pxColliderShape } from "./pxColliderShape";
export class pxSphereColliderShape extends pxColliderShape {
    constructor() {
        super();
        this._radius = 0.5;
        this._pxGeometry = new pxStatics._physX.PxSphereGeometry(this._radius);
        this._createShape();
    }
    setRadius(radius) {
        this._radius = radius;
        var maxScale = Math.max(this._scale.x, Math.max(this._scale.y, this._scale.z));
        this._pxGeometry.radius = this._radius * maxScale;
        this._pxShape.setGeometry(this._pxGeometry);
    }
    setOffset(position) {
        super.setOffset(position);
        this.setRadius(this._radius);
    }
    destroy() {
        super.destroy();
        this._radius = null;
    }
}
